import React,{useContext,useEffect,useCallback} from "react";
import NavbarNavigationItem from "./NavbarNavigationItem";
import {NavLink, useHistory} from 'react-router-dom'
import { AuthContext } from '../context/AuthContext'
import M from 'materialize-css'

function NavbarNavigation(props){
    const auth = useContext(AuthContext)
    const history = useHistory()
    

    const logoutHandler = event => {
        event.preventDefault()
        auth.logout()
        history.push('/')
      }
  

     if (auth.isAuthenticated ||auth.token){
         return(
            <>
            <ul className={props.class} id={props.id}>
                <NavbarNavigationItem  link="/" click={logoutHandler} title="" classAdditional={'hide-on-large-only'} class={props.classChild} classActive={props.classActiveChild}/>
                <NavbarNavigationItem  link="/admpage"  title=" " classAdditional={'hide-on-large-only'} class={props.classChild} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/" title="" class={props.classChild+" "+"hide-on-large-only"} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/gallery" title="" 
                class={props.classChild + " "+ 'dropdown-trigger' + ' '+ "hide-on-med-and-down"} dataTarget={"dropdown1"} 
                titleIcon={'arrow_drop_down'} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/gallery" title="" 
                class={props.classChild + " " + "hide-on-large-only"} 
                classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/events" title=" " 
                class={props.classChild + " " + "hide-on-large-only"} 
                classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/articles" title="" class={props.classChild} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/equipment" title="" class={props.classChild} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/documents" title="" class={props.classChild} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/contacts" title="" class={props.classChild} classActive={props.classActiveChild}/>
                <NavbarNavigationItem link="/about" title=" " class={props.classChild} classActive={props.classActiveChild}/>
                            
             </ul>
            </> 
         )
     }

    return(
        <>
        <ul className={props.class} id={props.id}>
        <NavbarNavigationItem link="/" title="" class={props.classChild+" "+"hide-on-large-only"} classActive={props.classActiveChild}/>
        <NavbarNavigationItem link="/gallery" title="" class={props.classChild + " "+'dropdown-trigger' + ' '+ "hide-on-med-and-down"} 
        dataTarget={"dropdown1"} classActive={props.classActiveChild} titleIcon={'arrow_drop_down'}/>
          <NavbarNavigationItem link="/gallery" title="" 
                class={props.classChild + " " + "hide-on-large-only"} 
                classActive={props.classActiveChild}/>
                 <NavbarNavigationItem link="/events" title=" " 
                class={props.classChild + " " + "hide-on-large-only"} 
                classActive={props.classActiveChild}/>
            <NavbarNavigationItem link="/articles" title="" class={props.classChild} classActive={props.classActiveChild}/>
            <NavbarNavigationItem link="/equipment" title="" class={props.classChild} classActive={props.classActiveChild}/>
            <NavbarNavigationItem link="/documents" title="" class={props.classChild} classActive={props.classActiveChild}/>
            <NavbarNavigationItem link="/contacts" title="" class={props.classChild} classActive={props.classActiveChild}/> 
            <NavbarNavigationItem link="/about" title=" " class={props.classChild} classActive={props.classActiveChild}/>         
         </ul>
        </>

    )
}

export default NavbarNavigation